$(function() {
  var scope = {
    timer: null
  };
  var page = {
    init: function() {
      $("#banner").on("click", ".dotlist li", page.tabBanner)
    },
    tabBanner: function() {
      $(".imglist").animate({
        "left": -1000 + "px"
      }, 1000);
      var _index = ã€€$(this).index();
      $(".dotlist li").removeClass("active");
      $(this).addClass("active");
      alert(-(_index * $(".imglist li").width()) + "px");
      $(".imglist").animate({
        "left": -(_index * $(".imglist li").width()) + "px"
      }, 1000);
      page.init();
      page.tabBanner();
    }
  }
})
